import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { faTimes } from '@fortawesome/free-solid-svg-icons';

export interface ServiceDetails{
  mainIcon: string;
  heading: string;
  headContent: {
    icon?: string;
    heading: string;
    desc: string;
  }[];
  aboutHeading: string;
  aboutContent: string;
  faqs: {
    title: string;
    content: string;
  }[]

}

@Component({
  selector: 'app-selected-service-details',
  templateUrl: './selected-service-details.component.html',
  styleUrls: ['./selected-service-details.component.css']
})
export class SelectedServiceDetailsComponent implements OnInit {

  @Output() closeComponent = new EventEmitter();
  closeIcon = faTimes;
  @Input() selectedServiceDetails: ServiceDetails;
  constructor() { }

  ngOnInit(): void {
    // console.log(this.selectedServiceDetails);
  }

  CloseComponent(){
    this.closeComponent.emit();
  }

}
