import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { Subject } from "rxjs";
import { environment } from "src/environments/environment";
import { BookingThankYouComponent } from "../components/app-payment/booking-thank-you/booking-thank-you.component";
import { OrderDetails } from "../models/cart.model";
import { TreatmentServices } from "../models/treatmentservicess.model";
import { AppService } from "./app.service";
import { AuthService } from "./auth.service";


const BASE_URL = environment.apiUrl + 'products/';

@Injectable()
export class TreatmentsService{

    
    private allServices: TreatmentServices[] = [];
    private allServicesUpdated = new Subject<{allServices: TreatmentServices[]}>()

    constructor(private oAppService: AppService,
         private http: HttpClient,
         private router: Router,
         private oAuthService: AuthService){

    }

    getPostsUpdateListener(){
        return this.allServicesUpdated.asObservable();
    }


    getAllServices(){
        this.http.get<{allServices: TreatmentServices[]}>(BASE_URL)
        .subscribe((retData) => {
            // console.log("2", retData);
            this.allServices = retData.allServices;
            this.allServicesUpdated.next({
                allServices: [...this.allServices]
            });
        })
        
    }

    placeOrder(orderDetails: OrderDetails){

        this.http.post<{ orderData: OrderDetails }>(BASE_URL + 'placeOrder', orderDetails)
        .subscribe(response => {
            this.router.navigate(['/treatments']);

            let bookingId: "KMS_1234"; //response.orderData.id;
            this.PlaceOrderSuccess(bookingId);

            this.updateOderDetailsInUserProfile();
        }, error => {
            // this.authStatusListener.next(false);
        })

        //remove from here
        this.PlaceOrderSuccess("KMS_123_123");
        this.updateOderDetailsInUserProfile();

        setTimeout(function () {

            this.oAppService.appLoading.next(false);
        
        }.bind(this), 2000);


    }

    updateOderDetailsInUserProfile(){
        let userId = localStorage.getItem('userId');
        this.oAuthService.getLoggedUserInfo(userId);
    }


    PlaceOrderSuccess(bookingId: string) {
        try {
            this.oAppService.BookingThankDialogRef = this.oAppService.dialog.open(BookingThankYouComponent, {
                width: '80%',
                position: { top: '10%' },
                panelClass: 'ScheduleDialog',
                autoFocus: false,
                disableClose: true,
                data: {
                    id: "1",
                    title: "Thank You !",
                    bookingID: bookingId
                }
            });
        } catch (error) {

        }
    }


}