import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { Subscription } from 'rxjs';
import { SETUP } from 'src/app/app.config';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-log-in',
  templateUrl: './log-in.component.html',
  styleUrls: ['./log-in.component.css']
})
export class LogInComponent implements OnInit {

  signInForm: FormGroup;
  isLoading: boolean = false;
  private authStatusSub: Subscription;
  isLogInClicked: boolean = false;
  constructor(public userService: AuthService, private formBuilder: FormBuilder) { }

  ngOnInit() {

    this.buildSignInForm();


    this.authStatusSub = this.userService.getAuthStatusListener().
      subscribe(authStstus => {
        this.isLoading = false;
      });
  }

  buildSignInForm() {
    this.signInForm = this.formBuilder.group({
      emailFC: ['', [Validators.required, Validators.pattern(SETUP.VALIDATORS.EMAIL)]],
      passwordFC: ['', [Validators.required]],
    }, { updateOn: 'change' });
  }

  onLogin() {
    this.isLogInClicked = true;
    console.log("Print: " + this.isLoading)
    if (this.signInForm.valid) {



      this.isLoading = true;
      this.userService.logIn(this.signInForm.value.emailFC, this.signInForm.value.passwordFC);
    }
  }

  ngOnDestroy() {
    this.authStatusSub.unsubscribe();
  }

}
