import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

export interface DialogDataInt{
  message: string;
  type: string;
}

@Component({
  selector: 'app-error-bar',
  templateUrl: './error-bar.component.html',
  styleUrls: ['./error-bar.component.css']
})
export class ErrorBarComponent implements OnInit {

  message: string = 'An unknown error occured';
  constructor(public dialogRef: MatDialogRef<ErrorBarComponent>, @Inject(MAT_DIALOG_DATA) public data: DialogDataInt) { }

  ngOnInit(): void {
  }

  close() {
    this.dialogRef.close();
  }

}
