import { Component } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { AppService } from 'src/app/services/app.service';
import { AuthService } from 'src/app/services/auth.service';
import * as _ from 'underscore';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'KMS';
  isLoading: boolean = false;

  private appLoadingSub: Subscription;
  constructor(private authService: AuthService,
    private router: Router,
    private oAppService: AppService){

  }

  ngOnInit(){
    this.authService.autoAuthUser();
    this.router.navigate(['/treatments']);
    // this.router.navigate(['/servicesinfo']);

  //   this.router.events.subscribe((evt) => {
  //     if (!(evt instanceof NavigationEnd)) {
  //         return;
  //     }
  //     window.scrollTo(0, 0)
  // });
  
  this.isLoadingListener();
  
  }


  isLoadingListener(){
    this.appLoadingSub = this.oAppService.getAppLoadingListener().
    subscribe( loadStatus => {
      this.isLoading = loadStatus;
    });
  }
  


}
