import { TryCatchStmt } from "@angular/compiler";
import { Injectable } from "@angular/core";
import { MatDialog, MatDialogRef } from "@angular/material/dialog";
import { Subject } from "rxjs";
import { BookingThankYouComponent } from "../components/app-payment/booking-thank-you/booking-thank-you.component";
import { PaymentDialogComponent } from "../components/app-payment/payment-dialog/payment-dialog.component";
import { ScheduleDialogBoxComponent } from "../components/app-payment/schedule-dialog-box/schedule-dialog-box.component";
import { CartModel } from "../models/cart.model";
import { ServiceItemDetail } from "../models/serviceslisting.model";


@Injectable()
export class AppService {

    CartItems: CartModel[] = [];
    CartTotal: number = 0;
    SafetyFee: number = 49;

    AppointmentSchedule: {
        date: string,
        day: string,
        time: string,
        fullDate: Date
    } = {
            date: "",
            day: "",
            time: "",
            fullDate: new Date()
        }

    cartItemsListener = new Subject<boolean>();
    displayCartListener = new Subject<boolean>();
    appLoading = new Subject<boolean>();

    public PaymentDialogRef: MatDialogRef<PaymentDialogComponent> = null;
    public BookingThankDialogRef: MatDialogRef<BookingThankYouComponent> = null;
    public ScheduleDialogRef: MatDialogRef<ScheduleDialogBoxComponent> = null;

    constructor(public dialog: MatDialog) {

    }

    getAppLoadingListener(){
        return this.appLoading.asObservable();
    }

    getCartItemsListener() {
        return this.cartItemsListener.asObservable();
    }

    getDisplayCartListener() {
        return this.displayCartListener.asObservable();
    }


    addItemDetailsToCart(item: ServiceItemDetail, packType: string) {
        if(packType !==""){
            let itemDetails: CartModel = {
                itemId: item.id,
                name: item.name,
                package: packType,
                category: item.category,
                price: +item.price,
                discount: "0"
            }
            this.CartItems.push(itemDetails);
        }else{
            let itemDetails: CartModel = {
                itemId: item.id,
                name: item.name,
                category: item.category,
                price: +item.price,
                discount: "0"
            }
            this.CartItems.push(itemDetails);
        }

        this.UpdateCartTotal();
    }


    removeItemDetailsFromCart(item: ServiceItemDetail) {
        try {
            for (let i = (this.CartItems.length - 1); i > -1; i--) {
                if (this.CartItems[i].itemId === item.id) {
                    this.CartItems.splice(i, 1);
                }
            }
            this.UpdateCartTotal();
        } catch (error) {

        }
    }

    UpdateCartTotal() {
        let total = 0;
        for (let i = 0; i < this.CartItems.length; i++) {
            total += this.CartItems[i].price;
        }
        this.CartTotal = total;
        if (this.CartItems.length === 0) {
            this.CartTotal = 0;
        }
        this.cartItemsListener.next(true);
        // console.log(this.CartTotal);
    }

    getCartTotal() {
        return this.CartTotal;
    }

    getTotalItemsCount() {
        return this.CartItems.length;
    }







    

}