  AddBodyDetoxToCart(item: ServiceItemDetail) {

    let price = 0;
    let packCount = this.bodyDetox.items.length;
    for(let i=0 ; i< this.bodyDetox.items.length ; i++){
      price += this.bodyDetox.items[i].price * this.bodyDetox.items[i].count;
    }
    let packType = "";
    if(packCount === 3){
      price = price*3;
      packType = "3 months"
    }else if(packCount === 2){
      packType = "1 month"
    }else{
      packType = "1 session";
    }
    

    item.price = price;
    this.AddToCart(item, packType);
  }

  AddTreatment1ToCart(item: ServiceItemDetail) {
    item.price = this.acneTreat.price * this.acneTreat.count;
    let packType = this.acneTreat.count + " session";
    this.AddToCart(item, packType);
  }

