import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { AuthService } from 'src/app/services/auth.service';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';
import { AppUser } from 'src/app/models/user.model';
import { UserProfileComponent } from '../../app-user/user-profile/user-profile.component';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  userAuthOptions: string[] = ["Register", "Login", "Logout"];
  authListenerSubs: Subscription;
  isLoggedIn: boolean = false;
  loggedUser;
  loggedUserSub: Subscription;

  dialogRef: MatDialogRef<UserProfileComponent>;
  popoverIsVisible: boolean = false;
  constructor(public authService: AuthService,
    private router: Router,
    public dialog: MatDialog) { }

  
    ngOnInit() {

      this.isLoggedIn = this.authService.isAuthenticated;
      this.authListenerSubs = this.authService.getAuthStatusListener()
      .subscribe(isAuth => {
        this.isLoggedIn = isAuth;
      });
  
      this.loggedUser  = this.authService.getLoggedInUser();
      // this.loggedUserSub = this.authService.getLoggedInUserListener()
      // .subscribe(user => {
      //   this.loggedUser = user;
      // })
  
    }

    ShowPopOver(event){
      this.popoverIsVisible = true;
    }
  
    HidePopOver(event){
      this.popoverIsVisible = false;
    }
  
    onSignUp(event, ProfilePopover){
      // ProfilePopover.close();
      this.router.navigate(['/signup']);
    }
  
    onLogin(event){
      this.router.navigate(['/login']);
    }
  
    openMyProfile(event){
  
      this.router.navigate(['/myprofile']);
  
    }
  
    onLogout(event){
      this.authService.logout();
    }


}
