import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { CreditCardValidators } from 'angular-cc-library';
import { OrderDetails } from 'src/app/models/cart.model';
import { AppService } from 'src/app/services/app.service';
import { TreatmentsService } from 'src/app/services/treatments.service';
import { BookingThankYouComponent } from '../booking-thank-you/booking-thank-you.component';
import { ScheduleDialogBoxComponent, DialogData } from '../schedule-dialog-box/schedule-dialog-box.component';


export interface CreditUPIDialogData {
  id: string;
  title: string;
  amount: number;
}


@Component({
  selector: 'app-payment-dialog',
  templateUrl: './payment-dialog.component.html',
  styleUrls: ['./payment-dialog.component.css']
})
export class PaymentDialogComponent implements OnInit {


  submitted = false;
  cardForm: FormGroup;

  upiSubmitted = false;
  upiForm: FormGroup;
  upiPattern = '[a-zA-Z0-9\.\-]{2,256}\@[a-zA-Z][a-zA-Z]{2,64}';
  arrowRightIcon = faArrowRight;
  private BookingThankDialogRef: MatDialogRef<BookingThankYouComponent> = null;

  CartItemsList: string[] = [];
  CartTotal: number = 0;
  CartCategoriesList: string[] = [];
  ServiceDate: Date;
  constructor(
    public dialogRef: MatDialogRef<ScheduleDialogBoxComponent>,
    @Inject(MAT_DIALOG_DATA) public data: CreditUPIDialogData,
    private oAppService: AppService, private formBuilder: FormBuilder,
    public dialog: MatDialog, private router: Router,
    private oTreatmentService: TreatmentsService) {

  }

  ngOnInit(): void {

    this.storeOrderDetails();

    this.buildCardForm();
    this.buildUPIForm();
  }

  buildCardForm() {

    this.cardForm = this.formBuilder.group({
      CardNumber: ['', [Validators.required, CreditCardValidators.validateCCNumber]],
      Validity: ['', [Validators.required, CreditCardValidators.validateExpDate]],
      CVVCode: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(4)]],
      NickName: ['', []],
    }, { updateOn: 'change' });

  }

  buildUPIForm() {
    this.upiForm = this.formBuilder.group({
      UPIID: ['', [Validators.required, Validators.pattern(this.upiPattern)]]
    }, { updateOn: 'change' });
  }




  onCardPay() {
    this.submitted = true;
    if (this.cardForm.valid) {
      console.log("Proceed Card Payment gateway");
    }

  }

  onUPIPay() {
    this.upiSubmitted = true;

    if (this.upiForm.valid) {
      console.log("Proceed UPI gateway");
    }
  }

  onCODPay() {
    setTimeout(function () {

      this.oAppService.dialog.closeAll();
      this.router.navigate(['/treatments']);

    }.bind(this), 500);

    this.prepareOrderAndPlace();


  }


  storeOrderDetails() {

    this.ServiceDate = this.oAppService.AppointmentSchedule.fullDate;

    this.CartTotal = this.oAppService.CartTotal;
    let CartItems = this.oAppService.CartItems;
    for (let i = 0; i < CartItems.length; i++) {
      this.CartItemsList.push(CartItems[i].name);
      this.CartCategoriesList.push(CartItems[i].category);
    }


  }

  prepareOrderAndPlace() {

    let orderDetails: OrderDetails = {
      id: "",
      itemsList: this.CartItemsList,
      categories: this.CartCategoriesList,
      description: "",
      date: this.ServiceDate,
      totalAmount: this.CartTotal,
    }
    this.oAppService.appLoading.next(true);
    setTimeout(function () {

    this.oTreatmentService.placeOrder(orderDetails);

    }.bind(this), 5000);

  }

  onCancelClick() {
    this.dialogRef.close("Close");
  }

}
