import { HttpClient, HttpErrorResponse } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "src/environments/environment";
import { IAppConfig } from "./app-config.model";


export const EVENTS: string[] = [
    "click",
    "keypress",
    "keydown",
    "scroll",
    "wheel",
    "mousedown"
];


export const SETUP = {

    VALIDATORS: {
        EMAIL: '[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+[.][A-Za-z]{2,3}$',
        ZIPCODE: '[0-9]+',
        ADHAAR: '[0-9]{12}',
        PAN: '[A-Z0-9]{10}',
    },
    SERVICEIMAGES:{
        IMG1: './assets/Icons/img1.jpg',
        IMG2: './assets/Icons/img2.jpg',
        IMG3: './assets/Icons/img3.jpg'
    },
    TIMES: {
        MINUTES: {
            NINETY: '90min',
            ONETWENTY: '120min',
            SIXTY: '60min'
        }
    }

};


@Injectable()
export class AppConfig {

    static settings: IAppConfig = null;

    constructor(private http: HttpClient) { }

    load() {
        const jsonFile = `assets/config/config.${environment.name}.json`;
        return new Promise<void>((resolve, reject) => {

            this.http.get(jsonFile, { responseType: "text" }).subscribe(data => {
                AppConfig.settings = JSON.parse(data);
                resolve();
            },
                function (error: HttpErrorResponse) {
                    reject(`Could not load file '${jsonFile}': ${JSON.stringify(error)}`);
                });
        });

    }

}