import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutUsComponent } from './components/app-kms-info/about-us/about-us.component';
import { ContactUsComponent } from './components/app-kms-info/contact-us/contact-us.component';
import { GalleryComponent } from './components/app-kms-info/gallery/gallery.component';
import { LogInComponent } from './components/app-user/log-in/log-in.component';
import { SignUpComponent } from './components/app-user/sign-up/sign-up.component';
import { TreatmentsComponent } from './components/app-home-page/treatments/treatments.component';
import { UserProfileComponent } from './components/app-user/user-profile/user-profile.component';
import { ServicesInfoComponent } from './components/app-home-page/services-info/services-info.component';


const routes: Routes = [
  {path: 'signup', component: SignUpComponent },
  {path: 'login', component: LogInComponent },
  {path: 'treatments', component: TreatmentsComponent },
  {path: 'gallery', component: GalleryComponent },
  // {path: 'edit/:productId', component: ProductcreateComponent, canActivate: [AuthGuard] },
  {path: 'aboutus', component: AboutUsComponent },
  {path: 'contactus', component: ContactUsComponent },
  {path: 'myprofile', component: UserProfileComponent},
  {path: 'servicesinfo', component: ServicesInfoComponent},



];

@NgModule({
  imports: [RouterModule.forRoot(routes, {scrollPositionRestoration: 'enabled'})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
