import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { faChevronRight, } from '@fortawesome/free-solid-svg-icons';
import { Subscription } from 'rxjs';
import { CartModel } from 'src/app/models/cart.model';
import { AppService } from 'src/app/services/app.service';
import { ScheduleDialogBoxComponent } from '../schedule-dialog-box/schedule-dialog-box.component';

@Component({
  selector: 'app-display-cart',
  templateUrl: './display-cart.component.html',
  styleUrls: ['./display-cart.component.css']
})
export class DisplayCartComponent implements OnInit, OnDestroy {

  ItemCount: number = 0;
  ItemPrice: number = 0;
  arrowIcon = faChevronRight;
  CartItems: CartModel[] = [];
  private cartTotalSub: Subscription;
  @Input() firstItems;
  @Output() closeDisplayCart = new EventEmitter<boolean>();
  @Output() openDisplayCart = new EventEmitter<boolean>();

  private ScheduleDialogRef: MatDialogRef<ScheduleDialogBoxComponent> = null;

  constructor(private oAppService: AppService, public dialog: MatDialog) { }

  ngOnInit(): void {

    if (this.firstItems !== null && this.firstItems !== undefined) {
      this.ItemCount = this.firstItems.count;
      this.ItemPrice = this.firstItems.price;
    }

    this.cartTotalSub = this.oAppService.getCartItemsListener().
      subscribe(updated => {
        this.CalculateTotalItems(updated);
      });
  }

  CalculateTotalItems(updated) {
    this.ItemPrice = this.oAppService.getCartTotal();
    this.ItemCount = this.oAppService.getTotalItemsCount();
    // console.log(this.ItemPrice);
  }

  onProceed() {

    this.oAppService.ScheduleDialogRef = this.oAppService.dialog.open(ScheduleDialogBoxComponent, {
      width: '50%',
      height: '70%',
      minHeight:'435px',
      position: { top: '10%' },
      panelClass: 'ScheduleDialog',
      autoFocus: false,
      hasBackdrop: true,
      disableClose: true,
      data: {}
    });

    this.oAppService.ScheduleDialogRef.afterClosed().subscribe(data => {
      if (data === "Close") {
        this.openDisplayCart.emit(false);
      } else {
        this.openDisplayCart.emit(false);
      }
      this.oAppService.cartItemsListener.next(true);
      this.oAppService.displayCartListener.next(true);
      this.oAppService.ScheduleDialogRef = null;
    });

    this.closeDisplayCart.emit(true);
  }

  ngOnDestroy() {
    this.cartTotalSub.unsubscribe();
  }

}
