export interface ServiceItemDetail {
    id: string,
    name: string,
    title: string,
    subTitle: string,
    category: string,
    sessions?: SessionItemInfo[],
    monthlyPack?: MonthlyPack[],
    rating: string,
    starRating: string,
    price: number,
    discount: number,
    time?: string,
    isAdded: boolean,
    images: string[],
    descriptionList: descObj[]
}

export interface MonthlyPack {
    name: string,
    items: MonthPackItem[],
}
